// 1.2. Button Styles
.btn{
    font-size: 16px;
    border-radius: 0;
    text-transform: uppercase;
    padding: 18px 55px;
    border: none;
    letter-spacing: 1px;
    border-radius: 50px;
}

.btn-primary{
    background-image: $primary-gradient;
    color: $white;
    position: relative;
    z-index: 1;
    box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
}

.btn-sm{
    padding: 15px 30px;
}