.steps{
    padding: 200px 0 100px;
    overflow-x: hidden;
    @include mobile{
        padding: 50px 0;
    }
    &-text{
        h2{
            margin-bottom: 27px;
        }
        p{
            margin-bottom: 45px;
            line-height: 28px;
        }
        @include desktop{
            text-align: center;
        }
    }
    &-content{
        background: $white;
        padding: 40px 15px;
        box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
        border-radius: 4px;
        position: relative;
        @include desktop{
            width: 70%;
            left: 50%;
            transform: translateX(-50%);
            margin-bottom: 50px;
        }
        @include mobile{
            width: 100%;
        }
        &::before, &::after{
            position: absolute;
            content: '';
            width: calc(100% - 80px);
            left: 50%; transform: translateX(-50%);
            top: 25%;
            height: 50px;
            background: #f8f8fa;
            border-radius: 4px;
            z-index: 1;
            @include mobile{
                display: none;
            }
        }
        &::after{
            top: 80%;
        }
        .line{
            &::before,&::after{
                position: absolute;
                content: '';
                width: 55%;
                left: 40px;
                top: 45%;
                height: 50px;
                background: #f8f8fa;
                border-radius: 4px;
                @include mobile{
                    display: none;
                }
            }
            &::after{
                width: 70%;
                top: 63%;
            }
        }
        .background{
            position: absolute;
            z-index: -1;
            left: -180px;
            top: -150px;
            @include desktop{
                left: 50%;
                transform: translateX(-50%);
                z-index: -2;
                width: 150%;
                img{
                    width: 100%;
                }
            }
            @include mobile{
                display: none;
            }
        }
        .step-item{
            display: flex;
            align-items: center;
            background: $white;
            box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
            padding: 25px 20px;
            // transform: translateX(-80px);
            border-radius: 5px;
            position: relative;
            z-index: 2;
            &:not(:last-child){
                margin-bottom: 50px;
            }
            .number{
                margin-right: 20px;
                span{
                    width: 66px;
                    height: 66px;
                    text-align: center;
                    background: $primary-gradient;
                    display: block;
                    line-height: 66px;
                    font-size: 25px;
                    color: $white;
                    box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
                    border-radius: 50%;
                }
            }
            .content{
                h6{
                    margin-bottom: 0;
                    color: $text-color;
                    font-weight: 500;
                    text-transform: uppercase;
                    font-size: 16px;
                }
            }
            &.featured{
                background: $violate-gradient;
                // transform: translateX(-180px);
                box-shadow: 0px 34px 46px 0px rgba(22, 25, 93, 0.23);
                .number{
                    span{
                        background: $white;
                        color: #943ec7;
                        box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
                    }
                }
                .content{
                    h6, p{
                        color: $white;
                    }
                }
            }
        }
    }
}