.cta{
    position: relative;
    @include desktop-4k{
        padding: 170px 0;
    }
    @include mobile{
        padding-top: 0;
    }
    .right-shape{
        position: absolute;
        right: 0; bottom: 27%;
        width: 60%;
        height: 100%;
        z-index: -2;
        @include desktop-4k{
            bottom: 42%;
        }
        img{
            width: 100%;
            height: auto;
        }
        @include desktop-xl{
            width: 80%;
            bottom: 20%;
        }
        @include desktop{
            width: 120%;
            bottom: -30%;
        }
        @include tablet{
            display: none;
        }
    }
    &-image{
        text-align: center;
        position: relative;
        @include mobile{
            overflow-x: hidden;
        }
        .background{
            position: absolute;
            top: -5%;
            left: -15%;
            z-index: -1;
            @include desktop{
                left: 50%;
                transform: translateX(-50%);
            }
            @include mobile{
                top: 0;
            }
        }
    }
    &-content{
        h2{
            margin-bottom: 27px;
        }
        p{
            line-height: 28px;
            margin-bottom: 60px;
            a{
                color: $primary-color;
            }
        }
        @include desktop{
            text-align: center;
        }
    }
}

.cta-col{
    @include mobile{
        padding: 0;
    }
}