.about{
    &-item{
        padding: 70px 40px 50px;
        transition: .3s ease;
        .icon{
            position: relative;
            @include size(80px);
            background: $white;
            display: block;
            border-radius: 50%;
            box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
            margin: 0 auto 45px;
            transition: .3s ease;
            &::before{
                content: '';
                position: absolute;
                top: 0; left: 0;
                background-image: $primary-gradient;
                transition: .3s ease;
                @include size(100%);
                transform: scale(0);
                transform-origin: center center;
                z-index: 1;
                border-radius: 50%;
            }
            i{
                position: relative;
                z-index: 2;
                line-height: 80px;
                font-size: 30px;
                &::before{
                    transition: .3s ease;
                    background-image: $primary-gradient;
                    background-clip: text;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
            }
        }
        h4{
            font-weight: 500;
            margin-bottom: 20px;
        }
        p{
            line-height: 28px;
        }
        &:hover{
            background: $white;
            box-shadow: 0px 25px 25px 0px rgba(22, 25, 93, 0.07);
            .icon{
                box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
                &::before{
                    transform: scale(1);
                }
                i{
                    &::before{
                        background-image: unset;
                        background-clip: none;
                        -webkit-background-clip: none;
                        -webkit-text-fill-color: $white;
                    }
                }
            }
        }
    }
}