.footer{
    padding: 170px 0 140px;
    background: url('../images/backgrounds/footer-bg.png');
    background-size: cover;
    background-position: top center;
    @include desktop-4k{
        padding: 220px 0 190px;
    }
    &-content{
        img{
            display: block;
            margin: 0 auto 45px;
        }
        ul.links{
            padding-bottom: 50px;
            border-width: 0 0 2px 0; border-style: solid;
            border-color: rgba($color: $white, $alpha: .10);
            margin-bottom: 25px;
            @include mobile{
                text-align: center;
            }
            li.list-inline-item{
                a{
                    color: $white;
                    text-decoration: none;
                    position: relative;
                    z-index: 1;
                    &::before{
                        content: '';
                        position: absolute;
                        width: 80%;
                        height: 8px;
                        background: $primary-gradient;
                        z-index: -1;
                        left: 0; bottom: 4px;
                        transform: scaleX(0);
                        transform-origin: right;
                        transition: transform .3s ease-in-out;
                    }
                    &:hover{
                        &::before{
                            transform: scaleX(1);
                            transform-origin: left;
                        }
                    }
                }
                @include mobile{
                    margin: 10px 20px;
                }
                &:not(:last-child){
                    margin-right: 40px;
                    @include mobile{
                        margin: 10px 20px;
                    }
                }
            }
        }
        .copyright-text{
            p{
                color: $white;
            }
        }
    }
}