.banner{
    padding: 150px 0 180px;
    background-image: url('../images/backgrounds/hero-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom center;
    @include desktop-4k{
        padding: 200px 0 230px;
    }
    &-text{
        margin-top: 100px;
        h1,p{
            color: $white;
        }
        h1{
            margin-bottom: 20px;
            @include mobile{
                font-size: 40px;
            }
        }
        p{
            line-height: 30px;
            width: 65%;
            margin-bottom: 60px;
            @include desktop{
                width: 80%;
                margin: 0 auto 60px;
            }
            @include mobile{
                width: 100%;
                margin: 0 0 30px;
            }
        }
        @include desktop{
            text-align: center;
        }
        @include mobile{
            margin-top: 50px;
        }
    }
    &-image{
        position: relative;
        .image{
            img{
                width: 100%;
            }
            @include desktop{
                width: 80%;
            }
        }
        .icon{
            position: absolute !important;
            width: 85px !important;
            height: 85px !important;
            border-radius: 50%;
            text-align: center;
            line-height: 85px;
        }
        .love{
            background-image: $primary-gradient;
            box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
            top: 7% !important; left: 19% !important;
        }
        .hand{
            background-image: $secondary-gradient;
            top: 24% !important; right: -24% !important; left: unset !important;
            @include desktop-xl{
                right: -10% !important;
            }
            @include desktop-lg{
                right: -5% !important;
            }
            @include desktop{
                right: 2% !important;
            }
        }
        .chat{
            background-image: $secondary-gradient;
            top: 55% !important; left: 4% !important; 
        }
        .message{
            background-image: $primary-gradient;
            box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
            top: 50% !important; right: -7% !important; left: unset !important;
            @include desktop-xl{
                right: 0% !important;
            }
            @include desktop{
                right: 15% !important;
            }
        }
    }
}