// Color Variables
$body-color: #f4f5f6;
$white: #fff;
$primary-color: #6643ec;
$secondary-color: #ff4545;
$black: #000;
$text-color: #777777;
$text-color-dark: #182345;
$text-color-light: #acacac;
$border-color: #dedede;
$footer-background-color: #272E34;

// Gradients
$primary-gradient: linear-gradient( 90deg, rgb(238,68,95) 0%, rgb(246,125,77) 100%);
$secondary-gradient: linear-gradient( 90deg, rgb(109,167,251) 0%, rgb(63,131,243) 100%);
$violate-gradient: linear-gradient( 67deg, rgb(148,62,199) 0%, rgb(60,55,191) 100%);

// Font Variables
$primary-font: 'Roboto', sans-serif;
$icon-font: 'themify';