.testimonial{
    position: relative;
    padding: 120px 0 200px;
    .background{
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        img{
            width: 100%;
            height: 100%;
        }
    }
    &-slider{
        &-item{
            outline: 0;
            img{
                margin: 0 auto 40px;
                border-radius: 50%;
                border: 10px solid #e6e1f9;
            }
            h4,h6{
                font-weight: 400;
            }
            h4{
                color: #555555;
                margin-bottom: 10px;
            }
            h6{
                color: $text-color;
                margin-bottom: 35px;
            }
            p{
                line-height: 28px;
            }
        }
        .slick-dots{
            bottom: -80px;
            li{
                button{
                    text-align: center;
                    line-height: 30px;
                    @include size(30px);
                    &::before{
                        font-size: 8px;
                        transition: .5s ease;
                    }
                }
                &.slick-active{
                    button{
                        &::before{
                            color: $secondary-color;
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}